.root {
	align-items: center;
	display: flex;
	justify-content: center;
	height: 100vh;
	width: 100vw;
}

.btnStack {
	align-items: center;
	bottom: 50px;
	display: flex;
	flex-direction: column;
	height: 100px;
	justify-content: space-between;
	left: 50px;
	position: absolute;
	z-index: 2;
}

.sideBtn {
	height: 40px;
	width: 40px;
}

.rotatable {
	transition: 0.3s;
}

.closeMode {
	transform: rotate(45deg);
	transition: 0.3s;
}

.stack {
	position: relative;
	width: 50%;
}

.mapLayer {
	aspect-ratio: 1.29;
}

.mapBackground {
	z-index: -1;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.inputOverlayBg {
	align-items: center;
	background-color: rgba(0, 0, 0, 0.8);
	display: flex;
	height: 100vh;
	justify-content: center;
	position: absolute;
	width: 100vw;
	z-index: 1;
}

.inputBg {
	border-radius: 1rem;
	background-color: #333;
	height: 50vh;
	width: 25vw;
}

.entryBg {
	background-color: #444;
	border-radius: 1rem;
	width: 25vw;
}

.nWidth {
	margin-left: 3rem;
	overflow-x: hidden;
	transition: 0.2s;
	width: 25vw;
}

.zWidth {
	overflow-x: hidden;
	transition: 0.2s;
	width: 0px;
}
